// 导入样式
import '../styles/main.scss';

// ============================================
// 配置
// ============================================

const CONFIG = {
  headerThreshold: 50,
  backToTopThreshold: 500,
  animationThreshold: 0.1,
  animationRootMargin: '0px 0px -50px 0px',
  counterDuration: 2000,
} as const;

// ============================================
// DOM 元素引用
// ============================================

const DOM = {
  header: document.getElementById('header') as HTMLElement,
  navToggle: document.getElementById('navToggle') as HTMLButtonElement,
  navMenu: document.getElementById('navMenu') as HTMLElement,
  backToTop: document.getElementById('backToTop') as HTMLButtonElement,
  navLinks: document.querySelectorAll('.nav__link') as NodeListOf<HTMLAnchorElement>,
  stats: document.querySelectorAll('.stat__value') as NodeListOf<HTMLElement>,
  hero: document.getElementById('home') as HTMLElement,
  lightbox: document.getElementById('lightbox') as HTMLElement,
};

// ============================================
// 导航功能
// ============================================

class Navigation {
  private lastScrollY = 0;
  private ticking = false;

  constructor() {
    this.init();
  }

  private init(): void {
    // 移动端菜单切换
    DOM.navToggle?.addEventListener('click', () => this.toggleMobileMenu());

    // 点击导航链接关闭移动菜单
    DOM.navLinks.forEach(link => {
      link.addEventListener('click', () => this.closeMobileMenu());
    });

    // 平滑滚动
    this.setupSmoothScroll();

    // 滚动监听
    window.addEventListener('scroll', () => this.onScroll(), { passive: true });

    // 初始化活动状态
    this.updateActiveLink();
  }

  private toggleMobileMenu(): void {
    DOM.navToggle?.classList.toggle('nav__toggle--active');
    DOM.navMenu?.classList.toggle('nav__menu--active');
    document.body.style.overflow = DOM.navMenu?.classList.contains('nav__menu--active')
      ? 'hidden'
      : '';
  }

  private closeMobileMenu(): void {
    DOM.navToggle?.classList.remove('nav__toggle--active');
    DOM.navMenu?.classList.remove('nav__menu--active');
    document.body.style.overflow = '';
  }

  private setupSmoothScroll(): void {
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
      anchor.addEventListener('click', (e) => {
        const href = anchor.getAttribute('href');
        if (!href || href === '#') return;

        const target = document.querySelector(href);
        if (target) {
          e.preventDefault();
          const headerHeight = DOM.header?.offsetHeight || 0;
          const targetPosition = target.getBoundingClientRect().top + window.pageYOffset - headerHeight;

          window.scrollTo({
            top: targetPosition,
            behavior: 'smooth',
          });
        }
      });
    });
  }

  private onScroll(): void {
    if (!this.ticking) {
      window.requestAnimationFrame(() => {
        this.handleScroll();
        this.ticking = false;
      });
      this.ticking = true;
    }
  }

  private handleScroll(): void {
    const scrollY = window.pageYOffset;

    // Header 样式
    if (DOM.header) {
      if (scrollY > CONFIG.headerThreshold) {
        DOM.header.classList.add('header--scrolled');
      } else {
        DOM.header.classList.remove('header--scrolled');
      }
    }

    // 回到顶部按钮
    if (DOM.backToTop) {
      if (scrollY > CONFIG.backToTopThreshold) {
        DOM.backToTop.classList.add('back-to-top--visible');
      } else {
        DOM.backToTop.classList.remove('back-to-top--visible');
      }
    }

    // 更新活动链接
    this.updateActiveLink();

    this.lastScrollY = scrollY;
  }

  private updateActiveLink(): void {
    const sections = document.querySelectorAll('section[id]');
    const scrollY = window.pageYOffset;
    const headerHeight = DOM.header?.offsetHeight || 0;

    sections.forEach(section => {
      const sectionTop = (section as HTMLElement).offsetTop - headerHeight - 100;
      const sectionHeight = (section as HTMLElement).offsetHeight;
      const sectionId = section.getAttribute('id');

      if (scrollY > sectionTop && scrollY <= sectionTop + sectionHeight) {
        DOM.navLinks.forEach(link => {
          link.classList.remove('active');
          if (link.getAttribute('href') === `#${sectionId}`) {
            link.classList.add('active');
          }
        });
      }
    });
  }
}

// ============================================
// 计数动画
// ============================================

class CounterAnimation {
  private counters: Map<HTMLElement, {
    targetValue: number;
    suffix: string;
    startTime: number | null;
    startValue: number;
    animated: boolean;
  }> = new Map();

  constructor() {
    this.init();
  }

  private init(): void {
    DOM.stats.forEach(stat => {
      const targetValue = parseFloat(stat.dataset.count || '0');
      const suffix = stat.dataset.suffix || '';

      this.counters.set(stat, {
        targetValue,
        suffix,
        startTime: null,
        startValue: 0,
        animated: false,
      });
    });

    this.setupIntersectionObserver();
  }

  private setupIntersectionObserver(): void {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const stat = entry.target as HTMLElement;
            const counter = this.counters.get(stat);

            if (counter && !counter.animated) {
              counter.animated = true;
              this.animateCounter(stat, counter);
              observer.unobserve(stat);
            }
          }
        });
      },
      { threshold: 0.5 }
    );

    DOM.stats.forEach(stat => observer.observe(stat));
  }

  private animateCounter(element: HTMLElement, counter: {
    targetValue: number;
    suffix: string;
    startTime: number | null;
    startValue: number;
    animated: boolean;
  }): void {
    const animate = (currentTime: number) => {
      if (counter.startTime === null) {
        counter.startTime = currentTime;
      }

      const elapsed = currentTime - counter.startTime;
      const progress = Math.min(elapsed / CONFIG.counterDuration, 1);

      // 使用 easeOutQuart 缓动函数
      const easeOutQuart = 1 - Math.pow(1 - progress, 4);
      const currentValue = counter.startValue + (counter.targetValue - counter.startValue) * easeOutQuart;

      // 格式化数值
      if (counter.targetValue >= 100000) {
        element.textContent = `${(currentValue / 10000).toFixed(0)}万+${counter.suffix}`;
      } else if (counter.targetValue >= 1000) {
        // 检查是否为中文单位后缀
        if (counter.suffix && (counter.suffix.includes('万') || counter.suffix.includes('亿'))) {
          element.textContent = `${Math.floor(currentValue)}${counter.suffix}`;
        } else {
          element.textContent = `${(currentValue / 1000).toFixed(1)}k+${counter.suffix}`;
        }
      } else if (counter.targetValue % 1 !== 0) {
        element.textContent = `${currentValue.toFixed(1)}${counter.suffix}`;
      } else {
        element.textContent = `${Math.floor(currentValue)}${counter.suffix}`;
      }

      if (progress < 1) {
        requestAnimationFrame(animate);
      }
    };

    requestAnimationFrame(animate);
  }
}

// ============================================
// 滚动动画
// ============================================

class ScrollAnimation {
  private elements: HTMLElement[] = [];

  constructor() {
    this.init();
  }

  private init(): void {
    // 选择要动画的元素
    const selectors = [
      '.product-card',
      '.advantage-item',
      '.solution-card',
      '.case-card',
      '.benefit-item',
      '.download-card',
      '.contact-item',
      '.certificate-card',
      '.culture-item',
      '.timeline-item',
    ];

    selectors.forEach(selector => {
      document.querySelectorAll(selector).forEach(el => {
        this.elements.push(el as HTMLElement);
      });
    });

    this.setupIntersectionObserver();
  }

  private setupIntersectionObserver(): void {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry, index) => {
          if (entry.isIntersecting) {
            // 添加延迟，创建级联效果
            setTimeout(() => {
              (entry.target as HTMLElement).classList.add('fade-in--visible');
            }, index * 50);

            observer.unobserve(entry.target);
          }
        });
      },
      {
        threshold: CONFIG.animationThreshold,
        rootMargin: CONFIG.animationRootMargin,
      }
    );

    this.elements.forEach(el => {
      el.classList.add('fade-in');
      observer.observe(el);
    });
  }
}

// ============================================
// 回到顶部
// ============================================

class BackToTop {
  constructor() {
    this.init();
  }

  private init(): void {
    DOM.backToTop?.addEventListener('click', () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    });
  }
}

// ============================================
// Hero 轮播图
// ============================================

class HeroCarousel {
  private carousel: HTMLElement | null = null;
  private track: HTMLElement | null = null;
  private slides: HTMLElement[] = [];
  private prevBtn: HTMLElement | null = null;
  private nextBtn: HTMLElement | null = null;
  private indicators: HTMLElement[] = [];
  private currentIndex = 0;
  private autoplayInterval: number | null = null;

  constructor() {
    this.init();
  }

  private init(): void {
    this.carousel = document.getElementById('heroCarousel');
    this.track = document.getElementById('carouselTrack');
    this.prevBtn = document.getElementById('carouselPrev');
    this.nextBtn = document.getElementById('carouselNext');

    if (!this.carousel || !this.track) return;

    this.slides = Array.from(this.track.querySelectorAll('.hero__carousel-slide'));
    this.indicators = Array.from(this.carousel.querySelectorAll('.hero__carousel-indicator'));

    if (this.slides.length === 0) return;

    // 绑定按钮事件
    this.prevBtn?.addEventListener('click', () => this.prev());
    this.nextBtn?.addEventListener('click', () => this.next());

    // 绑定指示器事件
    this.indicators.forEach((indicator, index) => {
      indicator.addEventListener('click', () => this.goToSlide(index));
    });

    // 初始化显示第一张
    this.goToSlide(0);

    // 自动播放
    this.startAutoplay();

    // 鼠标悬停时暂停自动播放
    this.carousel.addEventListener('mouseenter', () => this.stopAutoplay());
    this.carousel.addEventListener('mouseleave', () => this.startAutoplay());
  }

  private goToSlide(index: number): void {
    if (index < 0) {
      index = this.slides.length - 1;
    } else if (index >= this.slides.length) {
      index = 0;
    }

    this.currentIndex = index;

    // 更新slide显示
    this.slides.forEach((slide, i) => {
      if (i === index) {
        slide.classList.add('active');
      } else {
        slide.classList.remove('active');
      }
    });

    // 更新指示器
    this.indicators.forEach((indicator, i) => {
      if (i === index) {
        indicator.classList.add('active');
      } else {
        indicator.classList.remove('active');
      }
    });
  }

  private next(): void {
    this.goToSlide(this.currentIndex + 1);
  }

  private prev(): void {
    this.goToSlide(this.currentIndex - 1);
  }

  private startAutoplay(): void {
    this.stopAutoplay();
    this.autoplayInterval = window.setInterval(() => {
      this.next();
    }, 4000);
  }

  private stopAutoplay(): void {
    if (this.autoplayInterval) {
      clearInterval(this.autoplayInterval);
      this.autoplayInterval = null;
    }
  }
}

// ============================================
// Lightbox - 图片全屏查看
// ============================================

class Lightbox {
  private lightbox: HTMLElement | null = null;
  private overlay: HTMLElement | null = null;
  private closeBtn: HTMLElement | null = null;
  private image: HTMLElement | null = null;
  private prevBtn: HTMLElement | null = null;
  private nextBtn: HTMLElement | null = null;
  private carouselImages: HTMLImageElement[] = [];
  private currentIndex = 0;

  constructor() {
    this.init();
  }

  private init(): void {
    this.lightbox = document.getElementById('lightbox');
    this.overlay = document.getElementById('lightboxOverlay');
    this.closeBtn = document.getElementById('lightboxClose');
    this.image = document.getElementById('lightboxImage');
    this.prevBtn = document.getElementById('lightboxPrev');
    this.nextBtn = document.getElementById('lightboxNext');

    if (!this.lightbox || !this.overlay || !this.closeBtn || !this.image) return;

    // 获取所有轮播图图片
    this.carouselImages = Array.from(document.querySelectorAll('.hero__carousel-image'));

    // 为每张轮播图添加点击事件
    this.carouselImages.forEach((img, index) => {
      img.style.cursor = 'pointer';
      img.addEventListener('click', () => this.open(index));
    });

    // 关闭按钮
    this.closeBtn.addEventListener('click', () => this.close());

    // 上一张按钮
    this.prevBtn?.addEventListener('click', () => this.prev());

    // 下一张按钮
    this.nextBtn?.addEventListener('click', () => this.next());

    // 点击遮罩层关闭
    this.overlay.addEventListener('click', () => this.close());

    // 键盘事件
    document.addEventListener('keydown', (e) => {
      if (!this.lightbox?.classList.contains('active')) return;

      if (e.key === 'Escape') {
        this.close();
      } else if (e.key === 'ArrowLeft') {
        this.prev();
      } else if (e.key === 'ArrowRight') {
        this.next();
      }
    });
  }

  private open(index: number): void {
    if (!this.image || !this.lightbox) return;

    this.currentIndex = index;

    // 设置图片源
    const img = this.carouselImages[index];
    (this.image as HTMLImageElement).src = img.src;
    this.image.setAttribute('alt', img.getAttribute('alt') || '');

    // 显示lightbox
    this.lightbox.classList.add('active');

    // 禁止背景滚动
    document.body.style.overflow = 'hidden';
  }

  private close(): void {
    if (!this.lightbox) return;

    this.lightbox.classList.remove('active');

    // 恢复背景滚动
    document.body.style.overflow = '';
  }

  private prev(): void {
    if (this.carouselImages.length === 0) return;

    this.currentIndex--;
    if (this.currentIndex < 0) {
      this.currentIndex = this.carouselImages.length - 1;
    }

    this.showImage(this.currentIndex);
  }

  private next(): void {
    if (this.carouselImages.length === 0) return;

    this.currentIndex++;
    if (this.currentIndex >= this.carouselImages.length) {
      this.currentIndex = 0;
    }

    this.showImage(this.currentIndex);
  }

  private showImage(index: number): void {
    if (!this.image || index < 0 || index >= this.carouselImages.length) return;

    const img = this.carouselImages[index];
    (this.image as HTMLImageElement).src = img.src;
    this.image.setAttribute('alt', img.getAttribute('alt') || '');
  }
}

// ============================================
// 页面加载完成
// ============================================

class App {
  private navigation!: Navigation;
  private counterAnimation!: CounterAnimation;
  private scrollAnimation!: ScrollAnimation;
  private backToTop!: BackToTop;
  private heroCarousel!: HeroCarousel;
  private mapTooltip!: MapTooltip;
  private lightbox!: Lightbox;

  constructor() {
    // 等待 DOM 加载完成
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => this.init());
    } else {
      this.init();
    }
  }

  private init(): void {
    // 初始化各个模块
    this.navigation = new Navigation();
    this.counterAnimation = new CounterAnimation();
    this.scrollAnimation = new ScrollAnimation();
    this.backToTop = new BackToTop();
    this.heroCarousel = new HeroCarousel();
    this.mapTooltip = new MapTooltip();
    this.lightbox = new Lightbox();

    // 添加页面加载完成的类
    document.body.classList.add('loaded');

    console.log('必诚科技官网已加载完成');

    // 保持对象引用（避免未使用警告）
    void this.mapTooltip;
    void this.lightbox;
  }
}

// ============================================
// 地图悬浮提示
// ============================================

class MapTooltip {
  private map: HTMLElement | null = null;
  private mapTitle: HTMLElement | null = null;
  private tooltip: HTMLElement | null = null;
  private tooltipText: HTMLElement | null = null;
  private provinces: HTMLElement[] = [];

  constructor() {
    this.init();
  }

  private init(): void {
    this.map = document.querySelector('.china-map');
    this.tooltip = document.getElementById('mapTooltip');
    this.mapTitle = document.querySelector('.partners__map h3');

    if (!this.map || !this.tooltip) return;

    this.tooltipText = document.getElementById('tooltipText');
    // 确保标题为空
    if (this.mapTitle) {
      this.mapTitle.textContent = '';
    }

    this.provinces = Array.from(this.map.querySelectorAll('.province'));

    this.provinces.forEach(province => {
      province.addEventListener('mouseenter', (e) => this.showTooltip(e));
      province.addEventListener('mouseleave', () => this.hideTooltip());
    });
  }

  private showTooltip(event: Event): void {
    const province = event.currentTarget as HTMLElement;
    const name = province.getAttribute('data-name');

    if (!name || !this.tooltipText || !this.tooltip || !this.mapTitle) return;

    // 在标题位置显示省份名称
    this.mapTitle.textContent = name;

    // 在地图上隐藏原来的 tooltip
    this.tooltip.style.display = 'none';
  }

  private hideTooltip(): void {
    if (this.mapTitle) {
      // 恢复为空
      this.mapTitle.textContent = '';
    }
  }
}

// 启动应用
new App();

// ============================================
// 导出（供其他模块使用）
// ============================================

export { App, Navigation, CounterAnimation, ScrollAnimation, HeroCarousel, MapTooltip, Lightbox };
